import React, { useState } from "react";
import "./Contact.css";
import { db } from "./Firebase";
import "firebase/firestore";
const Contact = () => {
  const [fullname, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  const [loader, setLoader] = useState(false);
  const formSubmit = (e) => {
    e.preventDefault();
    setLoader(true);
    db.collection("contacts")
      .add({
        fullname: fullname,
        phone: phone,
        email: email,
        message: message,
      })
      .then(() => {
        setLoader(false);
        alert("Your responce has been submitted üëç");
      })
      .catch((error) => {
        alert(error.message);
        setLoader(false);
      });

    setName("");
    setPhone("");
    setEmail("");
    setMessage("");
  };

  return (
    <>
      <div className="contact-main">
        <div className="row">
          <div className="col-md-12">
            <div className="mu-title">
              <h1 className="mainHeader">Contact us</h1>
              <h4>Feel free to reach us.</h4>
              <div className="commonBorder"></div>
            </div>
          </div>
        </div>
        <div className="contact_footer">
          <div className="contact_info mt-4">
            <div className="conatiner-fluid">
              <div className="row">
                <div className="col-lg-10 offset-lg-1 all_details">
                  {/* Address */}
                  <div className="contact_info_item d-flex justify-content-center align-items-center p-3 details address">
                    <div>
                      <i
                        class="fa fa-map-marker fa-2x px-3"
                        aria-hidden="true"
                      ></i>
                    </div>
                    <div className="contact_info_content">
                      <div className="contact_info_title">Our Address</div>
                      <div className="contact_info_text">
                        Head office: 105, Sidhari, Azamgarh, Uttar pradesh, India -276001
                        <br />
                        <hr />
                        Circle office: 105/A, BCC RESIDENCY, LALBAG, LUCKNOW, UTTAR PRADESH-226001
                      </div>
                    </div>
                  </div>

                  {/* phone number */}
                  <div className="contact_info_item d-flex justify-content-center align-items-center p-3 details call">
                    <i class="fa fa-phone fa-2x px-3" aria-hidden="true"></i>
                    <div className="contact_info_content">
                      <div className="contact_info_title">CALL US</div>
                      <div className="contact_info_text"><a href="tel:+91 8090575545">+91 8090575545</a></div>
                    </div>
                  </div>

                  {/* email number */}
                  <div className="contact_info_item d-flex justify-content-center align-items-center p-3 details">
                    <i class="fa fa-envelope fa-2x px-3" aria-hidden="true"></i>
                    <div className="contact_info_content">
                      <div className="contact_info_title">Email Us</div>
                      <div className="contact_info_text"><a href="mailto:Info@Neelimaindustries.Online">info@neelimaindustries.Online</a></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* Contact Us From */}

          <div className="contact_form mt-4" >
            <div className="container">
              <div className="row">
                <div className="col-lg-10 offset-lg-1">
                  <div className="contact_form_container py-5 details details_odd">
                    <div className="contact_form_title d-flex justify-content-center">
                      <h1>Get in Touch</h1>
                    </div>
                    <form class="margin" onSubmit={formSubmit}>
                      <div class="inputGroup">
                        <input type="text" required
                          value={fullname}
                          onChange={(e) => setName(e.target.value)}
                        />
                        <span class="inputBar"></span>
                        <label>Name</label>
                      </div>

                      <div class="inputGroup">
                        <input type="email" required
                          value={email}
                          onChange={(e) => setEmail(e.target.value)}
                        />
                        <span class="inputBar"></span>
                        <label>Email</label>
                      </div>

                      <div className="inputGroup">
                        <input type="number" required
                          value={phone}
                          onChange={(e) => setPhone(e.target.value)} />
                        <span className="inputBar"></span>
                        <label>Phone Number</label>
                      </div>

                      <div class="inputGroup">
                        <textarea type="textarea" required
                          value={message}
                          onChange={(e) => setMessage(e.target.value)}
                        ></textarea>
                        <span class="inputBar"></span>
                        <label>Message</label>
                      </div>
                      <button
                        className="btn-send"
                        type="submit"
                        style={{ background: loader ? "#ccc" : " rgb(2, 2, 110)" }}
                      >
                        Send
                      </button>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default Contact;
