import React from 'react';
import { NavLink } from 'react-router-dom';
import web from "../src/images/LogoM.png";

const Navbar = () => {
    return (
        <>
            <div className="container-fluid nav_bg">
                <div className="row">
                    <div className="col mx-auto">
                        <nav className="navbar navbar-expand-lg navbar-light bg-light">
                            <div className="container-fluid">
                                <div className="logo-css">
                                    <img src={web} className="img-fluid logo-img" alt="home img" />
                                    <NavLink
                                        exact className="navbar-brand"
                                        to="/"
                                    >
                                        Neelima
                                 </NavLink>
                                </div>
                                <button
                                    className="navbar-toggler"
                                    type="button"
                                    data-toggle="collapse"
                                    data-target="#navbarSupportedContent"
                                    aria-controls="navbarSupportedContent"
                                    aria-expanded="false"
                                    aria-label="Toggle navigation"
                                >
                                    <span className="navbar-toggler-icon"></span>
                                </button>
                                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                    <ol className="navbar-nav ml-auto white">
                                        <li className="nav-item">
                                            <NavLink activeClassName="menu_active" exact className="nav-link active" aria-current="page" to="/">Home</NavLink>
                                        </li>
                                        <li className="nav-item">
                                            <NavLink activeClassName="menu_active" exact className="nav-link" to="/about">About Us</NavLink>
                                        </li>
                                        <li className="nav-item">
                                            <NavLink activeClassName="menu_active" exact className="nav-link" to="/service">Services</NavLink>
                                        </li>
                                        <li className="nav-item">
                                            <NavLink activeClassName="menu_active" exact className="nav-link" to="/contact">Contact Us</NavLink>
                                        </li>
                                        <li className="nav-item">
                                            <NavLink activeClassName="menu_active" exact className="nav-link" to="/carrers">Carrers</NavLink>
                                        </li>
                                    </ol>
                                </div>
                            </div>
                        </nav>

                    </div>
                </div>
            </div>
        </>
    );
};
export default Navbar;